<template>
    <div class="dropdown mt-3">
        <button
            class="dropdown-toggle bg-orange-400 flex justify-between items-center w-full rounded-lg text-white px-5 py-2 mt-2"
            @click.prevent="toggleContacto" :class="{ 'bg-orange-500': mostrarContacto }">
            <span class="text-xs lg:text-sm">{{ item.nombre }}</span>
            <i :class="{ 'fa-solid fa-angle-down transform rotate-180': mostrarContacto }"
                class="fa-solid fa-angle-down transition duration-300 shadow-sm"></i>
        </button>
        <div class="dropdown-menu mx-2 px-4 py-3 rounded-lg transition-max-height duration-300 bg-white" :class="{
            'max-h-[500px]': mostrarContacto,
            'max-h-0': !mostrarContacto,
            'overflow-hidden': !mostrarContacto,
            'pt-0 pb-0': !mostrarContacto
        }">
            <div v-for="destinatario in item.destinatarios" :key="destinatario.id" class="my-1 w-full lg:p-2 lg:px-4 cursor-pointer hover:bg-slate-500 transition duration-200
             p-2 rounded-lg bg-slate-400 text-white text-xs lg:text-sm flex justify-between"> <span>{{
                 destinatario.nombre }}</span>
                <div class="flex gap-3 items-center">
                    <i class="fa-solid fa-pen-to-square hover:text-orange-300 transition duration-200" @click.prevent="editarDestinatario(destinatario.id)"></i>
                    <i class="fa-solid fa-trash hover:text-orange-300 transition duration-200" @click.prevent="eliminarDestinatario(destinatario.id)"></i>
                </div>
            </div>
        </div>
    </div>
</template>
<script>
import { ref } from 'vue';
export default ({
    name: "DropdownItem",
    props: {
        item: Array
    },
    emits: ['editar-destinatario', 'eliminar-destinatario'],
    setup(_, { emit }) {
        const mostrarContacto = ref(false);
        const toggleContacto = () => {
            mostrarContacto.value = !mostrarContacto.value;
        }
        const editarDestinatario = (id_destinatario) => {
            emit('editar-destinatario', {
                id_destinatario: id_destinatario
            })
        }
        const eliminarDestinatario = (id_destinatario) => {
            emit('eliminar-destinatario', {
                id_destinatario: id_destinatario
            })
        }
        return {
            mostrarContacto,
            toggleContacto,
            editarDestinatario,
            eliminarDestinatario
        }
    }
})
</script>
