<template>
    <div class="fixed inset-0 flex items-center justify-center z-[9999]">
        <div class="absolute inset-0 bg-black opacity-30 pointer-events-none"></div>
        <!-- Container modal -->
        <div class="relative z-10 p-4 bg-white rounded-xl lg:min-w-[350px] lg:max-w-[450px] min-w-[330px] max-w-[350px]">
            <form class="p-2 text-gray-700 w-full h-full text-sm">
                <h3 class="text-center font-bold text-lg">Editar destinatario</h3>
                <ItemForm @value-input="saveNombre" label="Nombre o sede" name="nombre"
                    placeholder="Inserte el nombre o sede" :value="nombre" />
                <ItemForm @value-input="saveUbigeo" label="Ubigeo" name="ubigeo" placeholder="Inserte su ubigeo" :value="ubigeo" />
                <ItemForm @value-input="saveDireccion" label="Dirección" name="direccion"
                    placeholder="Inserte su dirección" :value="direccion" />
                <ItemForm @value-input="saveTelefono" label="Teléfono" name="telefono" placeholder="Inserte su teléfono" :value="telefono"/>
            </form>
            <div class="btns flex gap-2 justify-center items-center pt-2">
                <button @click="closeModal"
                    class="bg-gray-400 hover:bg-gray-500 text-white text-sm p-2 px-8 rounded-xl transition duration-300">Cerrar</button>
                <button @click="saveDatos"
                    class="bg-blue-400 hover:bg-blue-500 text-white text-sm p-2 px-8 rounded-xl transition duration-300">Aceptar</button>
            </div>
        </div>
        <!-- Container modal -->
    </div>
</template>
  
<script>
import { onMounted, ref } from 'vue';
import ItemForm from '../generales/ItemForm.vue';
import { buscarClientesDestinatarios } from '@/services/DestinatariosService';

export default {
    name: 'ModalCreate',
    components: { ItemForm },
    emits: ['cancelar-editar', 'aceptar-editar'],
    props: {
        idDestinatario: String
    },
    setup(props, { emit }) {
        const nombre = ref('');
        const ubigeo = ref('');
        const direccion = ref('');
        const telefono = ref('');
        const closeModal = () => {
            emit('cancelar-editar')
        }
        const saveUbigeo = (value) => {
            ubigeo.value = value;
        }
        const saveDireccion = (value) => {
            direccion.value = value;
        }
        const saveNombre = (value) => {
            nombre.value = value;
        }
        const saveTelefono = (value) => {
            telefono.value = value
        }
        const saveDatos = () => {
            emit('aceptar-editar', {
                id: props.idDestinatario,
                nombre: nombre.value,
                ubigeo: ubigeo.value,
                direccion: direccion.value,
                telefono: telefono.value
            })
        }
        onMounted(() => {
            buscarClientesDestinatarios(props.idDestinatario)
                .then(response => {
                    nombre.value = response.data.destinatario[0].nombre;
                    ubigeo.value = response.data.destinatario[0].ubigeo;
                    direccion.value = response.data.destinatario[0].direccion;
                    telefono.value = response.data.destinatario[0].telefono;
                })
        })
        return {
            closeModal,
            saveTelefono,
            saveUbigeo,
            saveDireccion,
            saveNombre,
            saveDatos,
            nombre,
            ubigeo,
            direccion,
            telefono
        }
    },
};

</script>

