<template>
    <section class="min-h-full w-full pb-[5rem] lg:pb-[2rem]">
        <div class="mb-8 flex justify-between items-center">
            <h3 class="title-vista text-2xl font-semibold text-neutral-600">Administrar destinatarios</h3>
            <button @click="openModalCreate"
                class="py-2 px-3 rounded-xl bg-orange-400 text-white lg:text-sm text-xs hover:bg-orange-500 transition duration-300">
                Crear nuevo
            </button>
        </div>
        <div class="bg-transparent">
            <DropdownItems v-for="cliente in listaClientes" :key="cliente.id" :item="cliente"
                @editar-destinatario="editarDestinatario" @eliminar-destinatario="eliminarDestinatario" />
        </div>
        <transition name="modal" mode="out-in">
            <ModalCreate v-show="openModalV" @close-modal="closeModal" @save-datos="crearDestinatario" />
        </transition>
        <transition name="modal" mode="out-in">
            <ConfirmEdit v-if="openModalSuccess" title="Logrado" :mensaje="msgModalSucces"
                @close-modal="closeModalSuccess" />
        </transition>
        <transition name="modal" mode="out-in">
            <ModalError v-if="openModalError" title="Advertencia" :mensaje="msgModalError" @close-modal="closeModalError" />
        </transition>
        <transition name="modal" mode="out-in">
            <ModalDelete v-if="confirmDelete" @aceptar-eliminar="aceptarEliminar" @cancelar-eliminar="cancelarEliminar"
                pregunta="¿Estás seguro de eliminar a este destinatario?" />
        </transition>
        <transition name="modal" mode="out-in">
            <ModalEditar v-if="openModalEdit" @aceptar-editar="aceptarEditar" @cancelar-editar="cancelarEditar"
                :idDestinatario="idDestinatario" />
        </transition>
    </section>
</template>
<script>
import { onMounted, ref } from 'vue';
import { listarClientesDestinatarios, crearDestinatarios, eliminarDestinatarios, editarDestinatarios } from '@/services/DestinatariosService';
import store from '@/store'
import DropdownItems from '@/components/destinatarios/DropdownItems.vue';
import ModalCreate from '@/components/destinatarios/ModalCreate.vue';
import ModalDelete from '@/components/generales/ModalDelete.vue';
import ConfirmEdit from '@/components/generales/ConfirmEdit.vue';
import ModalEditar from '@/components/destinatarios/ModalEditar.vue';
import ModalError from '@/components/generales/ModalError.vue';
export default ({
    name: "AdminDestinatarios",
    components: {
        DropdownItems,
        ModalCreate,
        ModalError,
        ConfirmEdit,
        ModalDelete,
        ModalEditar
    },
    setup() {
        const listaClientes = ref([])
        const openModalV = ref(false);
        const openModalSuccess = ref(false);
        const openModalEdit = ref(false);
        const confirmDelete = ref(false);
        const idDestinatario = ref('')
        const openModalError = ref(false);
        const msgModalSucces = ref('');
        const msgModalError = ref('')
        const closeModal = () => {
            openModalV.value = false;
        }
        const openModalCreate = () => {
            openModalV.value = true;
        }
        const closeModalSuccess = () => {
            openModalSuccess.value = false;
            window.location.reload();
        }
        const closeModalError = () => {
            openModalError.value = false;
        }
        const cancelarEliminar = () => {
            idDestinatario.value = "";
            confirmDelete.value = false;
        }
        const cancelarEditar = () => {
            idDestinatario.value = "";
            openModalEdit.value = false;
        }
        const eliminarDestinatario = (data) => {
            idDestinatario.value = data.id_destinatario;
            confirmDelete.value = true;
        }
        const editarDestinatario = (data) => {
            idDestinatario.value = data.id_destinatario;
            openModalEdit.value = true;
        }
        const aceptarEditar = (data) => {
            if (data.nombre == "" || data.ubigeo == "" || data.direccion == "" || data.telefono == "") {
                openModalEdit.value = false;
                openModalError.value = true;
                msgModalError.value = "Los campos nombre, ubigeo y dirección no deben estar vacios"
            } else {
                editarDestinatarios(data)
                    .then(() => {
                        openModalEdit.value = false;
                        idDestinatario.value = "";
                        openModalSuccess.value = true
                        msgModalSucces.value = "Se ha editado correctamente el destinatario"
                    })
                    .catch((error) => {
                        idDestinatario.value = ""
                        openModalEdit.value = false;
                        openModalError.value = true;
                        msgModalError.value = error
                    })
            }
        }
        const aceptarEliminar = () => {
            eliminarDestinatarios(idDestinatario.value)
                .then(() => {
                    idDestinatario.value = ""
                    confirmDelete.value = false;
                    openModalSuccess.value = true
                    msgModalSucces.value = "Se ha eliminado el destinatario"
                })
                .catch((error) => {
                    idDestinatario.value = ""
                    confirmDelete.value = false;
                    openModalError.value = true;
                    msgModalError.value = error
                })
        }
        onMounted(() => {
            listarClientesDestinatarios(store.state.codempresa)
                .then(response => {
                    listaClientes.value = response.data.clientesdestinatarios;
                })
        })
        const crearDestinatario = (dataformulario) => {
            if (dataformulario.id_cliente == "" || dataformulario.nombre == "" || dataformulario.ubigeo == "" || dataformulario.direccion == "") {
                openModalV.value = false;
                openModalError.value = true;
                msgModalError.value = "Se debe elegir un cliente y los campos nombre, ubigeo y dirección son obligatorios"
            } else {
                crearDestinatarios(store.state.codempresa, dataformulario)
                    .then(() => {
                        openModalV.value = false;
                        openModalSuccess.value = true
                        msgModalSucces.value = "Se ha creado correctamente el destinatario"
                    })
                    .catch((error) => {
                        openModalV.value = false;
                        openModalError.value = true;
                        msgModalError.value = error
                    })
            }
        }
        return {
            idDestinatario,
            listaClientes,
            closeModal,
            closeModalSuccess,
            closeModalError,
            cancelarEliminar,
            cancelarEditar,
            aceptarEliminar,
            confirmDelete,
            openModalSuccess,
            msgModalSucces,
            msgModalError,
            openModalCreate,
            openModalError,
            openModalV,
            openModalEdit,
            crearDestinatario,
            eliminarDestinatario,
            editarDestinatario,
            aceptarEditar
        }
    }
})
</script>