import axios from 'axios';
const baseURL = process.env.VUE_APP_BASE_URL;

export function listarClientesDestinatarios(codempresa){
    return axios.get(`${baseURL}destinatarios/list?codempresa=${codempresa}`)
}

export function crearDestinatarios(codempresa, data){
    return axios.post(`${baseURL}destinatarios/create`,{
        codempresa: codempresa,
        id_cliente: data.id_cliente,
        nombre: data.nombre,
        ubigeo: data.ubigeo,
        direccion: data.direccion,
        telefono: data.telefono
    })
}

export function eliminarDestinatarios(idDestinatario){
    return axios.delete(`${baseURL}destinatarios/delete?idDestinatario=${idDestinatario}`)
}

export function buscarClientesDestinatarios(idDestinatario){
    return axios.get(`${baseURL}destinatarios/search?idDestinatario=${idDestinatario}`)
}

export function editarDestinatarios(data){
    return axios.put(`${baseURL}destinatarios/edit`,{
        id: data.id,
        nombre: data.nombre,
        ubigeo: data.ubigeo,
        direccion: data.direccion,
        telefono: data.telefono
    })
}