<template>
    <div class="fixed inset-0 flex items-center justify-center z-[9999]">
        <div class="absolute inset-0 bg-black opacity-30 pointer-events-none"></div>
        <!-- Container modal -->
        <div class="relative z-10 p-4 bg-white rounded-xl lg:min-w-[350px] lg:max-w-[450px] min-w-[330px] max-w-[350px]">
            <form class="p-2 text-gray-700 w-full h-full text-sm">
                <h3 class="text-center font-bold text-lg">Crear destinatario</h3>
                <SelectForm name="Eliga al cliente que estara relacionado" :options="ListselectCliente"
                    v-model="selectedCliente" @selected="handleClienteSelected"
                    class="col-span-2 md:col-span-1 lg:col-span-2 text-sm my-2" />
                <ItemForm @value-input="saveNombre" label="Nombre o sede" name="nombre"
                    placeholder="Inserte el nombre o sede" value="" />
                <ItemForm @value-input="saveUbigeo" label="Ubigeo" name="ubigeo" placeholder="Inserte su ubigeo" value="" />
                <ItemForm @value-input="saveDireccion" label="Dirección" name="direccion"
                    placeholder="Inserte su dirección" />
                <ItemForm @value-input="saveTelefono" label="Teléfono" name="telefono"
                    placeholder="Inserte su teléfono" />
            </form>
            <div class="btns flex gap-2 justify-center items-center pt-2">
                <button @click="closeModal"
                    class="bg-gray-400 hover:bg-gray-500 text-white text-sm p-2 px-8 rounded-xl transition duration-300">Cerrar</button>
                <button @click="saveDatos"
                    class="bg-blue-400 hover:bg-blue-500 text-white text-sm p-2 px-8 rounded-xl transition duration-300">Crear</button>
            </div>
        </div>
        <!-- Container modal -->
    </div>
</template>
  
<script>
import { onMounted, ref } from 'vue';
import ItemForm from '../generales/ItemForm.vue';
import SelectForm from '../generales/SelectForm.vue';
import { listarClientes } from '@/services/ClientesService';
import store from '@/store'

export default {
    name: 'ModalCreate',
    components: { ItemForm, SelectForm },
    emits: ['close-modal', 'save-datos'],
    setup(_, { emit }) {
        const ListselectCliente = ref([]);
        const selectedCliente = ref('');
        const nombre = ref('');
        const ubigeo = ref('');
        const direccion = ref('');
        const telefono = ref('');
        const closeModal = () => {
            emit('close-modal')
        }
        const saveUbigeo = (value) => {
            ubigeo.value = value;
        }
        const saveDireccion = (value) => {
            direccion.value = value;
        }
        const saveNombre = (value) => {
            nombre.value = value;
        }
        const saveTelefono = (value) => {
            telefono.value = value
        }
        const saveDatos = () => {
            emit('save-datos', {
                id_cliente: selectedCliente.value,
                nombre: nombre.value,
                ubigeo: ubigeo.value,
                direccion: direccion.value,
                telefono: telefono.value
            })
        }
        const handleClienteSelected = (value) => {
            selectedCliente.value = value;
        };
        onMounted(() => {
            listarClientes(store.state.codempresa)
                .then(response => {
                    ListselectCliente.value = transformDataClientes(response.data.clientes);
                })
        })
        return {
            ListselectCliente,
            selectedCliente,
            handleClienteSelected,
            closeModal,
            saveTelefono,
            saveUbigeo,
            saveDireccion,
            saveNombre,
            saveDatos
        }
    },
};
function transformDataClientes(data) {
    const transformedData = data.map(item => ({ key: item.id, name: item.cliente }));
    transformedData.unshift({ key: '', name: 'Eliga un cliente' });
    return transformedData;
}
</script>

